@import "~styles/variables";  // '~' informs sass-loader that is not relative path

.launchPad__title {
	@extend .category__title; }

.launchPad__copyContainer {
	border-top: 1px solid $emphasized-color;
	display: flex;
	justify-content: space-around; }

.launchPad__specification, .launchPad__stats {
	displa: flex;
	flex-direction: column;
	justify-content: space-around;
	width: 50%;
	border-left: dotted 2px $specification-color;
	margin-top: 25px;

	p {
		color: $specification-color;
		padding-left: 35px;
		margin: 0;
		text-transform: uppercase; } }
